<template>
  <div class="modal-overlay" v-if="show">
    <div class="modal template-editor">
      <h2>{{ isEditing ? '–†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞—Ç—å —à–∞–±–ª–æ–Ω' : '–°–æ–∑–¥–∞—Ç—å —à–∞–±–ª–æ–Ω' }}</h2>
      
      <input v-model="templateName" placeholder="–ù–∞–∑–≤–∞–Ω–∏–µ —à–∞–±–ª–æ–Ω–∞" class="template-input" />
      
      <div class="questions-editor">
        <h3>–í–æ–ø—Ä–æ—Å—ã</h3>
        <div v-for="(question, index) in questions" :key="index" class="question-item">
          <div class="question-header">
            <input v-model="question.question" placeholder="–¢–µ–∫—Å—Ç –≤–æ–ø—Ä–æ—Å–∞" class="question-text" />
            <select v-model="question.type" class="question-type">
              <option value="text">–¢–µ–∫—Å—Ç</option>
              <option value="textarea">–î–ª–∏–Ω–Ω—ã–π —Ç–µ–∫—Å—Ç</option>
              <option value="radio">–í—ã–±–æ—Ä –≤–∞—Ä–∏–∞–Ω—Ç–∞</option>
              <option value="scale">–®–∫–∞–ª–∞ –æ—Ü–µ–Ω–∫–∏</option>
              <option value="matrix">–ú–∞—Ç—Ä–∏—Ü–∞ –æ—Ü–µ–Ω–æ–∫</option>
            </select>
            <button @click="removeQuestion(index)" class="remove-btn">–£–¥–∞–ª–∏—Ç—å</button>
          </div>
          
          <div v-if="question.type === 'radio'" class="options-editor">
            <h4>–í–∞—Ä–∏–∞–Ω—Ç—ã –æ—Ç–≤–µ—Ç–æ–≤:</h4>
            <div v-for="(option, optIndex) in question.options" :key="optIndex" class="option-item">
              <input v-model="option.text" placeholder="–¢–µ–∫—Å—Ç –≤–∞—Ä–∏–∞–Ω—Ç–∞" />
              <button @click="removeOption(index, optIndex)" class="remove-option-btn">√ó</button>
            </div>
            <button @click="addOption(index)" class="add-option-btn">–î–æ–±–∞–≤–∏—Ç—å –≤–∞—Ä–∏–∞–Ω—Ç</button>
          </div>
          
          <div v-if="question.type === 'scale'" class="scale-editor">
            <label>–ú–∏–Ω–∏–º—É–º: <input v-model.number="question.min" type="number" /></label>
            <label>–ú–∞–∫—Å–∏–º—É–º: <input v-model.number="question.max" type="number" /></label>
          </div>
          
          <div v-if="question.type === 'matrix'" class="matrix-editor">
            <h4>–°—Ç—Ä–æ–∫–∏ –º–∞—Ç—Ä–∏—Ü—ã:</h4>
            <div v-for="(row, rowIndex) in question.rows" :key="rowIndex" class="matrix-row">
              <input v-model="row.text" placeholder="–¢–µ–∫—Å—Ç —Å—Ç—Ä–æ–∫–∏" />
              <button @click="removeMatrixRow(index, rowIndex)" class="remove-option-btn">√ó</button>
            </div>
            <button @click="addMatrixRow(index)" class="add-option-btn">–î–æ–±–∞–≤–∏—Ç—å —Å—Ç—Ä–æ–∫—É</button>
            
            <h4>–ö–æ–ª–æ–Ω–∫–∏ –º–∞—Ç—Ä–∏—Ü—ã:</h4>
            <div v-for="(col, colIndex) in question.columns" :key="colIndex" class="matrix-col">
              <input v-model="col.text" placeholder="–¢–µ–∫—Å—Ç –∫–æ–ª–æ–Ω–∫–∏" />
              <button @click="removeMatrixCol(index, colIndex)" class="remove-option-btn">√ó</button>
            </div>
            <button @click="addMatrixCol(index)" class="add-option-btn">–î–æ–±–∞–≤–∏—Ç—å –∫–æ–ª–æ–Ω–∫—É</button>
          </div>
        </div>
        <button @click="addQuestion" class="add-question-btn">–î–æ–±–∞–≤–∏—Ç—å –≤–æ–ø—Ä–æ—Å</button>
      </div>
      
      <div class="modal-buttons">
        <button @click="saveTemplate" class="confirm-btn">–°–æ—Ö—Ä–∞–Ω–∏—Ç—å</button>
        <button @click="$emit('close')" class="cancel-btn">–û—Ç–º–µ–Ω–∞</button>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'TemplateEditor',
  props: {
    show: Boolean,
    template: Object,
    surveyType: String
  },
  data() {
    return {
      templateName: '',
      questions: []
    }
  },
  computed: {
    isEditing() {
      return !!this.template
    }
  },
  watch: {
    template: {
      handler(newTemplate) {
        if (newTemplate) {
          this.templateName = newTemplate.name
          this.questions = JSON.parse(JSON.stringify(newTemplate.questions))
        } else {
          this.resetForm()
        }
      },
      immediate: true
    }
  },
  methods: {
    resetForm() {
      this.templateName = ''
      this.questions = []
    },
    addQuestion() {
      this.questions.push({
        id: Date.now(),
        type: 'text',
        question: '',
        required: false
      })
    },
    removeQuestion(index) {
      this.questions.splice(index, 1)
    },
    addOption(questionIndex) {
      if (!this.questions[questionIndex].options) {
        this.questions[questionIndex].options = []
      }
      this.questions[questionIndex].options.push({ text: '', value: '' })
    },
    removeOption(questionIndex, optionIndex) {
      this.questions[questionIndex].options.splice(optionIndex, 1)
    },
    addMatrixRow(questionIndex) {
      if (!this.questions[questionIndex].rows) {
        this.questions[questionIndex].rows = []
      }
      this.questions[questionIndex].rows.push({ text: '', value: '' })
    },
    removeMatrixRow(questionIndex, rowIndex) {
      this.questions[questionIndex].rows.splice(rowIndex, 1)
    },
    addMatrixCol(questionIndex) {
      if (!this.questions[questionIndex].columns) {
        this.questions[questionIndex].columns = []
      }
      this.questions[questionIndex].columns.push({ text: '', value: '' })
    },
    removeMatrixCol(questionIndex, colIndex) {
      this.questions[questionIndex].columns.splice(colIndex, 1)
    },
    async saveTemplate() {
      try {
        const token = localStorage.getItem('token')
        
        if (!this.templateName.trim()) {
          alert('–ù–∞–∑–≤–∞–Ω–∏–µ —à–∞–±–ª–æ–Ω–∞ –æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ')
          return
        }
        
        if (!this.questions || this.questions.length === 0) {
          alert('–î–æ–±–∞–≤—å—Ç–µ —Ö–æ—Ç—è –±—ã –æ–¥–∏–Ω –≤–æ–ø—Ä–æ—Å')
          return
        }
        
        for (let i = 0; i < this.questions.length; i++) {
          const question = this.questions[i]
          if (!question.question || !question.question.trim()) {
            alert(`–í–æ–ø—Ä–æ—Å ${i + 1}: –¢–µ–∫—Å—Ç –≤–æ–ø—Ä–æ—Å–∞ –æ–±—è–∑–∞—Ç–µ–ª–µ–Ω`)
            return
          }
          if (!question.type) {
            alert(`–í–æ–ø—Ä–æ—Å ${i + 1}: –¢–∏–ø –≤–æ–ø—Ä–æ—Å–∞ –æ–±—è–∑–∞—Ç–µ–ª–µ–Ω`)
            return
          }
        }
        
        const templateData = {
          name: this.templateName,
          survey_type: this.surveyType,
          questions: this.questions
        }
        
        if (this.isEditing) {
          console.log('Making PUT request to:', `/api/survey-templates/${this.template.id}`)
          const response = await axios.put(`/api/survey-templates/${this.template.id}`, templateData, {
            headers: { Authorization: `Bearer ${token}` }
          })
          
          if (response.data.template_id) {
            console.log('New template created with ID:', response.data.template_id)
            alert('–®–∞–±–ª–æ–Ω —Å–æ—Ö—Ä–∞–Ω–µ–Ω –∫–∞–∫ –Ω–æ–≤—ã–π –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å—Å–∫–∏–π —à–∞–±–ª–æ–Ω!')
          } else {
            console.log('Template updated successfully')
            alert('–®–∞–±–ª–æ–Ω –æ–±–Ω–æ–≤–ª–µ–Ω —É—Å–ø–µ—à–Ω–æ!')
          }
        } else {
          console.log('Making POST request to:', '/api/survey-templates')
          await axios.post('/api/survey-templates', templateData, {
            headers: { Authorization: `Bearer ${token}` }
          })
          alert('–®–∞–±–ª–æ–Ω —Å–æ–∑–¥–∞–Ω —É—Å–ø–µ—à–Ω–æ!')
        }
        
        this.$emit('saved')
        this.$emit('close')
      } catch (error) {
        console.error('Error saving template:', error)
        console.error('Response data:', error.response?.data)
        const errorMessage = error.response?.data?.error || error.response?.data?.message || error.message
        alert(`–û—à–∏–±–∫–∞ —Å–æ—Ö—Ä–∞–Ω–µ–Ω–∏—è —à–∞–±–ª–æ–Ω–∞: ${errorMessage}`)
      }
    }
  }
}
</script>

<style scoped>
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.template-editor {
  background: white;
  padding: 2rem;
  border-radius: 8px;
  max-width: 1600px;
  max-height: 80vh;
  overflow-y: auto;
  width: 90%;
}

.template-input, .question-text {
  width: 100%;
  padding: 14px 18px;
  margin-bottom: 1rem;
  border: 2px solid #e5e7eb;
  border-radius: 12px;
  font-size: 15px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Inter", "Roboto", sans-serif;
  transition: all 0.25s cubic-bezier(0.4, 0, 0.2, 1);
  background: #ffffff;
  color: #111827;
  box-sizing: border-box;
}

