  margin-top: 0.5rem;
}

.options-editor, .scale-editor, .matrix-editor {
  margin-top: 0.5rem;
  padding: 0.5rem;
  background: #f8f9fa;
  border-radius: 4px;
}

.option-item, .matrix-row, .matrix-col {
  display: flex;
